*{
  margin: 0;
  padding: 0;
  font-family: Inter, Serif;
  flex-wrap: wrap;
  text-decoration: none;
}


img{
  width: 100%;
}

.container{
  max-width: 80%;
  margin: 0 auto;
  padding: 2rem;
}

a:link, :visited{
  color: black;
}

.Login{
    height: 100vh;
    background: url(/school-page/static/media/Login.13654c68.png);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

h1{
    font-size: 3rem;
    color: white;
    text-align: center;
}

.form-container{
    border: 2px  rgba(64, 202, 240, 0.575) solid;
    border-radius: 1rem;
    background-color: white;
    display: flex;
    justify-content: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 1rem 2rem;
    margin-top: 6rem;
}

form{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

button{
    padding: 0.5rem 3rem;
    font-size: 1.5rem;
    background-color: white;
    border-radius: 0.5rem;
    border: 2px rgba(64, 202, 240, 0.6) solid;
    margin-top: 3rem;
}

input{
    width: 20rem;
    height: 3rem;
    margin: 2rem 0;
    padding-left: 0.5rem;
    border: 2px rgba(64, 202, 240, 0.575) solid;
    border-radius: 0.5rem;
}

.form-container div{
    display: flex;
    justify-content: center;
}
.sidebar{
    width: 90%;
    background-color: rgba(64, 202, 240, 0.575);
    height: 100vh;
}

.sidebar > div{
    margin-top: 1rem;
}

.close{
    display: flex;
    justify-content: flex-end;
}

.closeIcon{
    width: 2rem;
    margin-right: 0.4rem;
}
.logo{
    display: flex;
    justify-content: center;
}

.logo-icon{
    width: 6rem;
}

.icon{
    width: 2rem;
}

.menu-item{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-top: 2px white solid;
    border-bottom: 2px white solid;
}

.menu-item > h2{
    width: 10rem;
    font-weight: 1000;
}

.exit > button{
    padding: 0.5rem 2rem;
    background-color: rgba(8, 174, 238, 0.20);
    border: 2px white solid;
    border-radius: 0.4rem;
    font-size: 1.3rem;
    font-weight: 700;
}

.exit{
    display: flex;
    justify-content: center;
